.radio__item {
  position: relative;
  cursor: pointer;
}
.radio__input {
  width: 0;
  height: 0;
  opacity: 0;
  position: absolute;
}
.radio__input:checked + .radio__label:before {
  border: 1px solid var(--blue);
}
